<template>
  <div class="box">
    <LayoutBox one="新增活动">
      <h4>商品介绍</h4>
      <el-form ref="form" label-width="100px" :model="form" :rules="rules" size="small">
        <el-form-item label="套餐名称" required>
          <el-input v-model="teamForm.spellGroupName" style="width: 454px" maxlength="30" show-word-limit />
        </el-form-item>
        <el-form-item label="活动时间" required>
          <el-date-picker v-model="activeTime" type="datetimerange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" />
        </el-form-item>
        <el-form-item label="活动折扣率" required>
          <el-input v-model="teamForm.discount" style="width: 454px">
            <template slot="append">%</template>
          </el-input>
        </el-form-item>
        <el-form-item label="拼团人数" required>
          <el-input v-model="teamForm.numPeople" style="width: 454px" />
        </el-form-item>
        <el-form-item label="套餐描述" required>
          <el-input v-model="teamForm.activeDescribes" style="width: 454px" />
        </el-form-item>
        <el-form-item label="套餐图片" required>
          <el-upload
            class="avatar-uploader"
            action="http-request"
            :http-request="uploadSectionFile"
            :show-file-list="false"
          >
            <el-image
              v-if="teamForm.imgUrl"
              :src="teamForm.imgUrl"
              fit="contain"
              class="avatar"
            />
            <i v-else class="el-icon-plus avatar-uploader-icon" />
          </el-upload>
        </el-form-item>
      </el-form>
    </LayoutBox>
    <el-card v-if="blUpdata" class="box-card">
      <h4>已关联商品</h4>
      <el-table :data="oldGoodsAndActivity" style="width: 100%">
        <!-- <el-table-column prop="date" label="商品编号" /> -->
        <el-table-column prop="name" label="商品名称" />
        <el-table-column prop="shopId" label="供应商" />
        <el-table-column prop="originalPrice" label="销售价（元）" />
        <el-table-column label="成团人数">
          <template slot-scope="scope">
            <el-input v-if="oldUpdateIndex === scope.$index" />
            <p v-else>{{ scope.row.amount }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="date" label="操作" width="150">
          <template slot-scope="scope">
            <el-button v-if="oldUpdateIndex === scope.$index" type="text" @click="onOldSave">完成</el-button>
            <el-button v-else type="text" @click.native.prevent="onOldUpdate(scope)">编辑</el-button>
            <el-button type="text">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="goodsTotle" @current-change="handleCurrentChange" />
    </el-card>
    <el-card class="box-card">
      <h4>新增关联商品</h4>
      <el-table :data="goodsAndActivity" style="width: 100%">
        <!-- <el-table-column prop="code" label="商品编号" /> -->
        <el-table-column prop="name" label="商品名称" />
        <el-table-column prop="originalPrice" label="销售价（元）" />
        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <el-button type="text" @click="onDelete(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="add_goods_btn">
        <el-button class="add_goods" type="text" icon="el-icon-plus" @click.native.prevent="onAddGoods">添加商品</el-button>
      </div>
      <el-divider />
      <div class="btn_group">
        <el-button v-if="blUpdata" class="inquire" type="success" size="medium" @click="save_btn">保存</el-button>
        <el-button v-else class="inquire" type="success" size="medium" @click="submit_btn">提交</el-button>
        <router-link to="/super/market/team_buy/list"><el-button size="medium" style="width: 112px">取消</el-button></router-link>
      </div>
    </el-card>
    <el-dialog title="提示" :visible.sync="dialogVisible" fullscreen>
      <AddGoodsCmp ref="AddGoodsCmp" />
      <span slot="footer" class="dialog-footer">
        <el-button round @click="dialogVisible = false">取 消</el-button>
        <el-button type="danger" round @click="addAll()">添 加</el-button>
      </span>
    </el-dialog>

    <image-compressor-cmp ref="imageCompressorCmp" />
    
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import AddGoodsCmp from '../../add_goods_cmp/index'
import { MIXINS_BARGAINS_AND_TEAM_BUY_FORMRULES } from '@/mixins/form.js'
import { MIXINS_BARGAINS_AND_TEAM_BUY_ADDALL } from '@/mixins/event.js'
import { MIXINS_BARGAINS_AND_TEAM_BUY_DATA } from '@/mixins/data.js'
import { MIXINS_BARGAINS_AND_TEAM_BUY_CREATED } from '@/mixins/created.js'
import imageCompressorCmp from '@/components/image_compressor_cmp/index.vue'

export default {
  name: 'AddTeamBuy',

  components: {
    LayoutBox, AddGoodsCmp,
    imageCompressorCmp
  },

  data() {
    return {
      activeType: 0, // 活动类型 0-团购 1-秒杀
      activeTime: '', // 活动时间需要解析
      teamForm: {
        spellGroupName: '', // 拼团名称
        spellGroupTime: 24, // 拼团持续时间
        activityStart: '', // 开始时间
        activityStop: '', // 结束时间
        discount: '', // 折扣率
        activeDescribes: '', // 活动描述
        numPeople: '', // 拼团人数
        imgUrl: '', // 图片
      }
    }
  },

  mixins: [
    MIXINS_BARGAINS_AND_TEAM_BUY_FORMRULES,
    MIXINS_BARGAINS_AND_TEAM_BUY_ADDALL,
    MIXINS_BARGAINS_AND_TEAM_BUY_DATA,
    MIXINS_BARGAINS_AND_TEAM_BUY_CREATED,
  ],

  watch: {
    // 解析活动时间
    activeTime (newVal) {
      this.teamForm.activityStart = newVal[0]
      this.teamForm.activityStop = newVal[1]
    }
  },

  methods: {
    // 点击提交
    async submit_btn () {
      const smallProdId = this.goodsAndActivity.map((item) => {
        return item.id
      })
      const data = {
        smallProdId: smallProdId.join(','),
        ...this.teamForm
      }
      if (this.goodsAndActivity.length < 1) {
        this.$message.error('请先选择添加商品')
        return
      }
      if (this._formRules()) {
        const res = await this.$http.post(this.$api.insertSmallSpellGroup, data)
        if (res.data.success) {
          this.$message.success('成功发布活动')
          this.$router.push('/super/market/team_buy/list')
        } else {
          this.$message.error(res.data.msg)
        }
      }
    },
    // 点击保存
    save_btn () {
      if (this._formRules()) {
        this.apiUpdateMerActivity().then((res) => {
          if (res.data.success) {
            this.$message.success('更新活动成功')
            this.$router.push('/super/market/team_buy/list')
          } else {
            this.$message.error(res.data.msg)
          }
        })
      }
    },
    // 自定义覆盖主图图片上传方法
    async uploadSectionFile (params) {
      let formData = await this._uploadSectionFile(params)
      if (formData) {
        this._upload(formData)
          .then((res) => {
            this.teamForm.imgUrl = res
          })
      }
    },

    // 私有方法-----------------------------------------------
    // 上传图片前的处理
    async _uploadSectionFile (params) {
      const isJPG = params.file.type === 'image/jpeg' || params.file.type === 'image/png'
      const isLt2M = params.file.size / 1024 / 1024 < 10
      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG/PNG 格式!')
        return false
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 10MB!')
        return false
      }
      console.log(222222)
      const formData = await this.$refs.imageCompressorCmp._imageCompressor(params.file)
      return formData
    },
    // 上传图片
    _upload (formData) {
      return this.$http.post('/common/oss/uploadFile', formData).then(res => res.data.data.webUrl)
    },
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .box{
    min-width: 1100px;
    .time_group{
      display: flex;
    }
    .box-card{
      width: 100%;
      margin-top: 15px;
      .add_goods_btn{
        width: 100%;
        text-align: center;
        .add_goods{
          color: $themColor;
        }
      }
      .btn_group{
        width: 100%;
        text-align: center;
      }
    }
  }
  .el-icon-plus{
    border: 1px dashed #ccc;
  }
  .avatar-uploader .el-upload {
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
